.icon, .icon-text {
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  font-size: 24px;
  speak: none;
  font-family: "Icons" !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon {
  display: inline-block;
  width: 40px;
  height: 40px;
  text-align: center;
  line-height: 40px;
}

.icon-sm {
  width: 20px;
  height: 20px;
  line-height: 20px;
  font-size: 16px;
}

.icon-narrow {
  width: 24px;
}

.header {
  color: #fff;
  background: #2e99db;
}

.header__title {
  padding: 13px 20px;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
  text-align: center;
}

.toolbar_static .toolbar__col {
  vertical-align: middle;
  padding: 0 10px;
  height: 50px;
}

.toolbar_delimited {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.toolbar__container {
  display: table;
  table-layout: fixed;
  width: 100%;
}

.toolbar__row {
  display: table-row;
}

.toolbar__col {
  display: table-cell;
  vertical-align: top;
  font-size: 16px;
}

.toolbar__col:not(:last-child) {
  border-right: 1px solid rgba(0, 0, 0, 0.1);
}

.toolbar__col_refresh {
  width: 150px;
}

@media only screen and (max-width: 1200px) {
  .toolbar__col_refresh {
    width: 70px;
  }
}

@media only screen and (max-width: 1200px) {
  .toolbar__col_refresh .btn {
    font-size: 0;
  }
}

.toolbar__button i,
.toolbar__button strong {
  float: right;
}

.toolbar__button i {
  margin-right: -10px;
  height: auto;
  line-height: inherit;
}

.toolbar__button strong {
  font-size: 25px;
  font-weight: 400;
}

@media only screen and (max-width: 1200px) {
  .toolbar__button strong {
    font-size: 19px;
  }
}

@media only screen and (max-width: 1023px) {
  .toolbar__button strong {
    font-size: 16px;
  }
}

.search {
  position: relative;
}

.search__field {
  position: relative;
  padding-left: 50px;
}

.search__field:before {
  content: '';
  position: absolute;
  left: 10px;
  right: 0;
  bottom: 2px;
  border-bottom: 2px solid #fff;
  opacity: 0.7;
}

.search__input {
  padding: 0 10px;
  width: 100%;
  height: 50px;
  color: #fff;
  background: none;
  font-size: 16px;
  font-weight: 400;
  line-height: normal;
}

.search__input::placeholder {
  color: #fff;
  opacity: .7;
}

.search__controls {
  position: absolute;
  top: 0;
  left: 0;
  padding: 5px;
  font-size: 0;
}

.search__button {
  display: inline-block;
  vertical-align: top;
  color: #fff;
}

.search-dropdown {
  position: absolute;
  z-index: 100;
  top: 100%;
  left: 10px;
  right: 0;
  margin-top: 14px;
  opacity: 0;
  visibility: hidden;
  box-shadow: 0 0 4px 2px rgba(0, 0, 0, 0.15);
  transition: opacity 0.15s ease-in-out, visibility 0.15s ease-in-out;
}

.search-dropdown.is-open {
  opacity: 1;
  visibility: visible;
}

.search-dropdown__container {
  position: relative;
  z-index: 2;
  background: #fff;
}

.search-dropdown__body {
  overflow: auto;
  max-height: 199px;
}

.search-dropdown__list {
  padding: 0 12px;
}

.search-dropdown__item {
  padding: 10px 0;
  margin: 0 -5px;
  color: #333;
  font-size: 16px;
  cursor: pointer;
  transition: color 0.17s ease-in-out;
}

.search-dropdown__item:after {
  content: " ";
  display: table;
  clear: both;
}

.search-dropdown__item:not(:last-child) {
  border-bottom: 1px solid #dadada;
}

.no-touch .search-dropdown__item:hover {
  color: #2e99db;
}

.search-dropdown__price, .search-dropdown__text {
  padding: 0 5px;
}

.search-dropdown__price {
  float: right;
  font-weight: 400;
  white-space: nowrap;
}

.search-dropdown__text {
  overflow: hidden;
  font-weight: 300;
}

.search-dropdown__arrow {
  position: absolute;
  z-index: 1;
  left: 40px;
  bottom: 100%;
  width: 20px;
  height: 20px;
}

.search-dropdown__arrow:before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 14px;
  height: 14px;
  background: #fff;
  box-shadow: 0 0 4px 2px rgba(0, 0, 0, 0.15);
  transform: translate3d(-50%, 50%, 0) rotate(45deg);
}

.main-header {
  position: absolute;
  z-index: 20;
  top: 0;
  left: 0;
  right: 0;
  color: #fff;
  background: #2e99db;
}

.main-header__sidebar {
  position: relative;
  z-index: 20;
  float: left;
  width: 40%;
  box-shadow: -3px -5px 4px 5px rgba(0, 0, 0, 0.2);
}

.main-header__container {
  position: relative;
  float: right;
}

.main-header__fullinfo {
  position: absolute;
  z-index: 15;
  top: 0;
  right: 0;
  bottom: 0;
  box-shadow: -3px -5px 4px 5px rgba(0, 0, 0, 0.2);
  transform: translate3d(-110%, 0, 0);
  transition: transform 0.3s ease-in-out;
}

.main-header__fullinfo.is-open {
  transform: translate3d(0, 0, 0);
}

.main-header__sidebar, .main-header__container, .main-header__fullinfo {
  min-height: 50px;
  background: #2e99db;
}

.main-header__container, .main-header__fullinfo {
  width: 60%;
}

.main-header__container_full, .main-header__fullinfo_full {
  width: 100%;
}

.main-header__container_full .main-header__title, .main-header__fullinfo_full .main-header__title {
  text-align: center;
}

.main-header__menu {
  position: absolute;
  z-index: 50;
  top: 0;
  left: 0;
  width: 60px;
  height: 50px;
  color: #2e99db;
  background: #cae9f4;
  font-size: 32px;
  line-height: 50px;
  transition: color 0.175s ease, background 0.175s ease;
}

.main-header__menu:active {
  color: #fff;
  background: #a0d7eb;
}

.no-touch .main-header__menu:active {
  color: #fff;
  background: #a0d7eb;
}

.is-touch .main-header__menu {
  width: 80px;
}

.main-header__menu ~ .main-header__toolbar {
  padding-left: 60px;
}

.is-touch .main-header__menu ~ .main-header__toolbar {
  padding-left: 80px;
}

.main-header__help, .main-header__delete {
  position: absolute;
  top: 5px;
  right: 5px;
  color: #fff;
}

.main-header__delete + .main-header__toolbar {
  padding-right: 50px;
}

.main-header__sync {
  position: absolute;
  z-index: 10;
  top: 0;
  right: 0;
  height: 50px;
  padding: 5px 13px 5px 5px;
}

.main-header__sync .btn-sync {
  margin: -5px;
}

.main-header__indicator {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 8px;
  background: #ec4a41;
  transition: background 0.15s ease-in-out;
}

.main-header__indicator[data-state="active"] {
  background: #22B745;
}

.main-header__search-for {
  padding: 0 0 0 10px;
  margin: -5px -5px -5px 0;
  height: 50px;
  color: inherit;
  background: none;
  font-size: 16px;
  font-weight: 300;
}

.main-header__search-for:active {
  color: inherit;
  background: rgba(0, 0, 0, 0.1);
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.no-touch .main-header__search-for:active {
  color: inherit;
  background: rgba(0, 0, 0, 0.1);
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.main-header__search-for span, .main-header__search-for .icon {
  display: inline-block;
  vertical-align: middle;
}

.main-header__scan {
  position: absolute;
  z-index: 35;
  right: 0;
  bottom: 0;
  display: none;
}

.main-header__scan .btn-scan {
  color: #333;
}

.main-header__searchbox {
  max-width: 400px;
}

.main-header__searchbox_right {
  position: absolute;
  top: 0;
  right: 50px;
  width: 30%;
  max-width: none;
}

.main-header__toolbar .toolbar__col {
  border: none;
}

.main-header__title {
  padding-top: 13px;
  padding-bottom: 13px;
  margin: 0;
  font-size: 20px;
}

.main-header__back {
  position: absolute;
  z-index: 10;
  top: 0;
  left: 0;
  white-space: nowrap;
}

.main-header__back .main-header__title {
  display: inline-block;
  vertical-align: middle;
}

.main-header .is-hidden {
  display: none;
}

.sidebar {
  position: relative;
  z-index: 10;
  display: flex;
  flex-direction: column;
  background: #fff;
  box-shadow: -3px 5px 4px 5px rgba(0, 0, 0, 0.2);
  word-break: break-word;
}

@media only screen and (max-width: 1023px) {
  .sidebar {
    box-shadow: none;
  }
}

.sidebar_cart .sidebar__footer {
  padding: 0;
}

.sidebar__header, .sidebar__footer {
  flex: 0 0 auto;
}

.sidebar__header {
  overflow: hidden;
}

.sidebar__header-row {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.sidebar__header-row:after {
  content: " ";
  display: table;
  clear: both;
}

.sidebar__header-col {
  float: left;
  padding: 15px 25px;
  width: 50%;
  font-size: 16px;
}

@media only screen and (max-width: 480px) {
  .sidebar__header-col {
    width: 100%;
  }
}

.sidebar__header-col:not(:last-child) {
  border-right: 1px solid rgba(0, 0, 0, 0.1);
}

@media only screen and (max-width: 480px) {
  .sidebar__header-col:not(:last-child) {
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    border-right: 0;
  }
}

.sidebar__footer {
  padding: 15px 0;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  background: #d2edf6;
}

.sidebar__total {
  color: #333;
  font-size: 14px;
  line-height: 16px;
}

.sidebar__total-list:not(:first-child) {
  border-top: 1px solid rgba(146, 212, 233, 0.3);
}

.sidebar__total-item:after {
  content: " ";
  display: table;
  clear: both;
}

.sidebar__total-item:not(:first-child) {
  border-top: 1px dashed rgba(146, 212, 233, 0.3);
}

.sidebar__total-label, .sidebar__total-value {
  position: relative;
  float: left;
  padding: 3px 25px;
  width: 50%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.sidebar__total-value {
  text-align: right;
}

.sidebar__body {
  overflow: auto;
  flex: 1 1 auto;
}

.sidebar__item {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.sidebar__discount-icon {
  position: absolute;
  top: 50%;
  right: 0;
  margin: -8px 5px 0 0;
  width: 16px;
  fill: currentColor;
}

.sidebar-item {
  overflow: hidden;
  padding: 10px 25px;
}

.sidebar-item.is-highlight {
  background: #eee;
}

.sidebar-item__row {
  margin: 0 -5px;
}

.sidebar-item__row:after {
  content: " ";
  display: table;
  clear: both;
}

.sidebar-item__col {
  float: left;
  padding: 5px;
  width: 50%;
