html,
body {
	height: 100%;
	overflow: hidden;
	@include md {
		overflow: auto; } }
body {
	min-width: 320px;
	min-height: 570px;
	color: $darkGray;
	background: $alabaster;
	font-family: 'Roboto', Arial, sans-serif;
	font-size: 14px;
	font-weight: 300;
	line-height: 1.2; }

.app {
	position: relative;
	display: flex;
	flex-direction: column;
	overflow: auto;
	height: 100vh;
	@include md {
		height: auto;
		overflow: visible; } }

.main {
	display: flex;
	height: calc(100vh - 50px);
	@include md {
		display: block;
		height: auto; }
	&_cart {
		padding-top: 50px;
		height: 100vh; } }

.inner {
	display: flex;
	height: 100%;
	@include md {
		display: block;
		height: auto; } }

.sidebar {
	width: 40%;
	height: 100%;
	@include md {
		width: 100%;
		height: auto; } }

.sidebar ~ .container {
	width: 60%;
	@include md {
		width: 100%; } }

.container {
	overflow: auto;
	height: 100%;
	@include md {
		height: auto; }
	&_overlay {
		position: absolute;
		z-index: 5;
		top: 0;
		right: 0;
		bottom: 0;
		width: 60%;
		background: $alabaster;
		box-shadow: -3px 0 4px 5px rgba(0, 0, 0, 0.2);
		transform: translate3d(-110%,0,0);
		transition: transform 0.3s ease-in-out;
		&.is-open {
			@include gpu; }
		.container {
			&__body {
				padding-top: 50px;
				padding-bottom: 100px;
				height: 100%; }
			&__submit {
				position: absolute;
				bottom: 0;
				left: 0;
				right: 0;
				padding: 0 20px 20px; } } } }

.section {
	padding: 40px 20px; }

.text-right {
	text-align: right; }

.text-uppercase {
	text-transform: uppercase; }
